import React from 'react';
import Styles from '../styles/switches.module.scss';
import cn from '../utilities/classnames';
export const Switch = ({ className, disabled, id, offText, on, onText, onChange, value, }) => {
    return (<div className={cn(Styles['switch-toggle'], className, {
        [Styles['is-disabled']]: disabled,
    })}>
      <input checked={on} className={Styles['switch-checkbox']} disabled={disabled} id={id} onChange={onChange} type="checkbox" value={value}/>
      <label className={Styles['switch-label']} htmlFor={id}>
        <div className={cn(Styles['switch-option'], Styles['switch-option-off'])}>
          {offText}
        </div>
        <div className={cn(Styles['switch-option'], Styles['switch-option-on'])}>
          {onText}
        </div>
      </label>
      <div className={Styles['switch-selector']}/>
    </div>);
};
Switch.defaultProps = {
    offText: 'off',
    onText: 'on',
    value: '',
};
export default Switch;



