card-wrap + .email-card-add {\n  display: flex;\n  justify-content: center;\n}\n\n.email-card-stats {\n  display: flex;\n  flex-grow: 1;\n  justify-content: space-around;\n  width: 100%;\n  padding: 30px;\n  border: 1px solid $slate-20;\n  border-bottom: 0;\n  border-radius: $border-radius $border-radius 0 0;\n\n  .stat,\n  .label {\n    margin-bottom: 0;\n    text-align: center;\n  }\n\n  .stat {\n    margin-bottom: 0;\n    font-size: 20px;\n    line-height: 30px;\n  }\n\n  .delivered {\n    color: $stats-delivered;\n  }\n\n  .opens {\n    color: $stats-opens;\n  }\n\n  .unique-opens {\n    color: $stats-unique-opens;\n  }\n\n  .clicks {\n    color: $stats-clicks;\n  }\n\n  .unique-clicks {\n    color: $stats-unique-clicks;\n  }\n\n  .unsubscribes {\n    color: $stats-unsubscribes;\n  }\n\n  .spam-reports {\n    color: $stats-spam-reports;\n  }\n\n  .label {\n    margin-left: 0;\n    font-size: 12px;\n    font-weight: 600;\n    line-height: 15px;\n    text-transform: uppercase;\n  }\n}\n\n.email-card-stats + .email-card {\n  border-radius: 0 0 $border-radius $border-radius;\n}\n\n// absolute positioning to keep container/border 100% height\n.email-card-count {\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: 68px;\n  border-right: 1px solid $slate-20;\n\n  p {\n    font-weight: 600;\n    text-align: center;\n    text-transform: uppercase;\n    transform: rotate(270deg);\n  }\n}\n\n.email-card-content {\n  width: 166px;\n  padding: 18px;\n\n  a {\n    position: relative;\n    display: block;\n\n    &:hover .email-card-content-edit {\n      opacity: 1;\n    }\n  }\n\n  img {\n    display: block;\n    max-width: 100%;\n    border: 1px solid $slate-40;\n    border-radius: $border-radius;\n  }\n\n  :global .btn {\n    width: 130px;\n    padding: 28px 12px;\n  }\n\n  // offset by 1px on all sides to avoid covering border\n  .email-card-content-edit {\n    position: absolute;\n    t