  var __STORY__ = "import { storiesOf } from \"@storybook/react\";\nimport React, { Component } from \"react\";\nimport Switch from \"./index\";\nconst stories = storiesOf(\"Switch\", module);\nconst propsToState = props => props.on;\nclass SwitchContainer extends Component {\n  constructor() {\n    super(...arguments);\n    this.state = { on: propsToState(this.props) };\n    this.onChange = e => {\n      this.setState({ on: !this.state.on });\n    };\n  }\n  render() {\n    return (\n      <Switch\n        {...this.props}\n        id={this.props.id}\n        on={this.state.on}\n        onChange={this.onChange}\n      />\n    );\n  }\n}\nstories.add(\"Switch - On\", () => <SwitchContainer id=\"switch-example-on\" on />);\nstories.add(\"Switch - Off\", () => <SwitchContainer id=\"switch-example-off\" />);\nstories.add(\"Switch - Disabled\", () => (\n  <SwitchContainer id=\"switch-example-disabled\" disabled on />\n));\n";
  var __ADDS_MAP__ = {"@Switch - On":{"startLoc":{"col":12,"line":25},"endLoc":{"col":78,"line":25}},"@Switch - Off":{"startLoc":{"col":12,"line":26},"endLoc":{"col":77,"line":26}},"@Switch - Disabled":{"startLoc":{"col":12,"line":27},"endLoc":{"col":1,"line":29}}};
  
  import { storiesOf } from '@storybook/react';
import React, { Component } from 'react';
import Switch from './index';
const stories = storiesOf('Switch', module).addDecorator(withStorySource(__STORY__, __ADDS_MAP__));
const propsToState = (props) => props.on;
class SwitchContainer extends Component {
    constructor() {
        super(...arguments);
        this.state = { on: propsToState(this.props) };
        this.onChange = (e) => {
            this.setState({ on: !this.state.on });
        };
    }
    render() {
        return (<Switch {...this.props} id={this.props.id} on={this.state.on} onChange={this.onChange}/>);
    }
}
stories.add('Switch - On', () => <SwitchContainer id="switch-example-on" on/>);
stories.add('Switch - Off', () => <SwitchContainer id="switch-example-off"/>);
stories.add('Switch - Disabled', () => (<SwitchContainer id="switch-example-disabled" disabled on/>));

  


