  import { storiesOf } from '@storybook/react';
import React, { Component } from 'react';
import Button from '../src/button';
import ButtonList from '../src/button-list';
import { CenterModal } from '../src/center-modal';
const stories = storiesOf('CenterModal', module).addDecorator(withStorySource(__STORY__, __ADDS_MAP__));
// Need a way to close/open the modal
class ExampleContainer extends Component {
    constructor() {
        super(...arguments);
        this.state = { isOpen: true };
        this.open = (e) => {
            this.setState({ isOpen: true });
        };
        this.close = (e) => {
            this.setState({ isOpen: false });
        };
    }
    render() {
        return (<div>
        <Button type="primary" onClick={this.open}>
          Open Modal
        </Button>
        <CenterModal padding={this.props.padding} large={this.props.large} hasX={this.props.hasX} onClose={this.close} open={this.state.isOpen} renderBody={this.props.renderBody} renderFooter={this.props.renderFooter
            ? () => this.props.renderFooter(this.close)
            : null} renderHeader={this.props.renderHeader} data-role="example"/>
      </div>);
    }
}
stories.add('CenterModal with footer and header, no x', () => (<ExampleContainer renderHeader="Scorpion XING Zone " renderBody={() => (<p>
        Please do not feed the scorpions. Doing so could result in scorpion
        wounds, fines of up to $2,500 and up to 5 years of community service.
      </p>)} renderFooter={(close) => (<ButtonList>
        <Button small type="secondary" onClick={close}>
          Cancel
        </Button>
        <Button small type="primary" onClick={close}>
          Confirm
        </Button>
      </ButtonList>)}/>));
stories.add('CenterModal with footer and header, no x, large', () => (<ExampleContainer renderHeader="Scorpion XING Zone " large renderBody={() => (<p>
        Please do not feed the scorpions. Doing so could result in scorpion
        wounds, fines of up to $2,500 and up to 5 years of community service.
      </p>)} renderFooter={(close) => (<ButtonList>
        <Button small type="secondary" onClick={close}>
          Cancel
        </Button>
        <Button small type="primary" onClick={close}>
          Confirm
        </Button>
      </ButtonList>)}/>));
stories.add('CenterModal without footer or header, with x', () => (<ExampleContainer hasX renderBody={() => (<p>
        Please do not feed the scorpions. Doing so could result in scorpion
        wounds, fines of up to $2,500 and up to 5 years of community service.
      </p>)}/>));

  


