  import { action } from '@storybook/addon-actions';
import { storiesOf } from '@storybook/react';
import React, { Component } from 'react';
import { Checkbox } from '../src/checkbox';
const stories = storiesOf('Checkbox', module).addDecorator(withStorySource(__STORY__, __ADDS_MAP__));
class Example extends Component {
    constructor() {
        super(...arguments);
        this.state = { checked: false };
        this.onChange = () => {
            action('Checkbox changed');
            this.setState({ checked: !this.state.checked });
        };
    }
    render() {
        return (<Checkbox checked={this.state.checked} disabled={this.props.disabled} id={this.props.id} label={this.props.label} onChange={this.onChange}/>);
    }
}
stories.add('Standard Checkbox', () => (<Example id="standard-checkbox" label="Transactional"/>));
stories.add('Disabled Checkbox', () => (<Example id="disabled-checkbox" disabled label="Marketing"/>));

  


