import React, { Fragment, PureComponent } from 'react';
import Dropzone from './dropzone';
import FileUpload, { DroppedFile, FileSelect } from './file-upload';
class CSVUpload extends PureComponent {
    constructor() {
        super(...arguments);
        this.handleRemove = (callback) => {
            return (event) => {
                const { onRemove } = this.props;
                if (onRemove) {
                    onRemove(event);
                }
                callback(event);
            };
        };
    }
    render() {
        const { onChange, onRemove } = this.props;
        return (<FileUpload onFileSelect={onChange} render={({ hasFile, handleRemove, hovered, invalid, file, FileSelectLink, }) => (<Dropzone active={hasFile} hovered={hovered} invalid={invalid}>
            {file ? (<DroppedFile file={file} onRemove={this.handleRemove(handleRemove)}/>) : (<FileSelect>
                <Fragment>
                  Drag and drop your CSV file here or{' '}
                  <FileSelectLink>select a CSV file to upload</FileSelectLink>.
                </Fragment>
              </FileSelect>)}
          </Dropzone>)} supportedType={'text/csv'}/>);
    }
}
export default CSVUpload;



