  import { storiesOf } from '@storybook/react';
import React, { Component } from 'react';
import Button from '../src/button';
import FullscreenModal from '../src/full-screen-modal';
const stories = storiesOf('FullScreen Modal', module).addDecorator(withStorySource(__STORY__, __ADDS_MAP__));
class ExampleContainer extends Component {
    constructor() {
        super(...arguments);
        this.state = { isOpen: true };
        this.open = (e) => {
            this.setState({ isOpen: true });
        };
        this.close = (e) => {
            this.setState({ isOpen: false });
        };
    }
    render() {
        return (<div>
        <Button type="primary" onClick={this.open}>
          Open Modal
        </Button>
        <FullscreenModal isOpen={this.state.isOpen} title="Fullscreen Modal" onClose={this.close}/>
      </div>);
    }
}
stories.add('ConfirmationModal with plain contents', () => (<ExampleContainer renderHeader="Very cool! " renderBody="This will make a thing happen."/>));

  


