   vertical-align: text-bottom;\n  }\n}\n\nol,\nul {\n  font-size: 13px;\n  line-height: 20px;\n}\n\nstrong {\n  font-weight: 600;\n}\n\nem {\n  font-style: italic;\n}\n\nstrong em,\nem strong {\n  font-style: italic;\n  font-weight: 600;\n}\n\nabbr {\n  text-decoration: none;\n  background-image: linear-gradient(to right, $slate 33%, $white 0%);\n  background-position: bottom;\n  background-repeat: repeat-x;\n  background-size: 3px 1px;\n}\n\n// TODO: only used in tables-empty.njk\n// remove after verifying that nothing is broken (replace w/ .is-centered ?)\n.is-center {\n  text-align: center;\n}\n\n.error {\n  color: $ron-burgundy;\n}\n\nsmall,\n.small {\n  font-size: 13px;\n  line-height: 20px;\n}\n\n.mono {\n  font-family: 'Andale Mono', Monaco, Consolas, 'Lucida Console', monospace;\n}\n\n.highlighter {\n  background-color: $color-highlighter;\n}\n\n.is-weight-light {\n  font-weight: 300;\n}\n\n.is-weight-medium {\n  font-weight: 600;\n}\n","// sendgrid style-guide mixins\n// sass-lint:disable no-color-literals, no-vendor-prefixes\n// linting disabled due to mixins from 3rd parties\n\n// colfax\n@mixin colfax($font-weight: 400) {\n  font-family: Colfax, Helvetica, Arial, sans-serif;\n  font-style: normal;\n  font-weight: $font-weight;\n}\n\n// base icon font styles\n@mixin icon-font-base {\n  font-family: 'styleguideicons';\n  font-size: 16px;\n  font-style: normal;\n  font-weight: normal;\n  line-height: 1;\n  text-transform: none;\n  speak: none;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n// Dont hate needed this for select it uses the entirety of the mixins namespace\n:export {\n  font-family: 'styleguideicons';\n  font-size: 16px;\n  font-style: normal;\n  font-weight: normal;\n  line-height: 1;\n  text-transform: none;\n  speak: none;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n\n// transition\n@mixin transition($args...) {\n  transition: $args;\n}\n\n// transform\n@mixin transform($val) {\n  transform: $val;\n}\n\n// transform origin\n@mixin transform-origin($val) {\n  -webkit-transform-origin: $val;\n  -moz-transform-origin: $val;\n  -ms-transform-origin: $val;\n  transform-origin: $val;\n}\n\n// opacity\n@mixin opacity($trans) {\n  opacity: $trans;\n}\n\n// clear floats\n@mixin clearfix {\n  &::before,\n  &::after {\n    display: table;\n    content: '';\n  }\n\n  &::after {\n    clear: both;\n  }\n}\n\n// text image replacement\n@mixin text-hide {\n  font: 0/0 a;\n  color: transparent;\n  text-shadow: none;\n  background-color: transparent;\n  border: 0;\n}\n\n@mixin btn-shadow {\n  box-shadow: 0 1px 4px 0 rgba($sg-blue, 0.2);\n}\n\n@mixin dropdown-shadow {\n  box-shadow: 0 2px 6px 0 rgba($slate, 0.2);\n}\n\n// labels and statuses\n@mixin label($color) {\n  color: $color;\n\n  &::before {\n    background-color: $color;\n  }\n}\n\n// tooltip svg arrows\n@mixin svg-arrow($color, $position: up) {\n  $width: 18px;\n  $height: 6px;\n  $degrees: 0;\n  @if ($position == down) {\n    $degrees: 180 18 6;\n  } @else if ($position == left) {\n    $degrees: -90 18 18;\n    $width: 6px;\n    $height: 18px;\n  } @else if ($position == right) {\n    $degrees: 90 6 6;\n    $width: 6px;\n    $height: 18px;\n  }\n  width: $width;\n  height: $height;\n  background: no-repeat\n    url('data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D%22http://www.w3.org/2000/svg%22%20width%3D%22#{$width * 2}%22%20height%3D%22#{$height * 2}%22%3E%3Cpath%20fill%3D%22#{$color}%22%20transform%3D%22rotate(#{$degrees})%22%20d%3D%22M2.658,0.000%20C-13.615,0.000%2050.938,0.000%2034.662,0.000%20C28.662,0.000%2023.035,12.002%2018.660,12.002%20C14.285,12.002%208.594,0.000%202.658,0.000%20Z%22/%3E%3C/svg%3E');\n  background-size: 100% auto;\n}\n\n// tooltip base effects\n@mixin base-effects {\n  @include opacity(0);\n  @include transition(all 0.2s ease-out 0.2s);\n  pointer-events: none;\n}\n\n// meter base percentage\n@mixin base-percentage-meter {\n  height: 8px;\n  border-radius: 4px;\n}\n\n// meter percentage\n@mixin percentage-meter($start, $stop) {\n  @include base-percentage-meter;\n  @include background-image(linear-gradient(center left, $start, $stop));\n}\n\n// connector and arrow for email-cards\n@mixin email-card-connector($color: $slate-20) {\n  &::before {\n    position: absolute;\n    right: 50%;\n    bottom: -25px;\n    width: 2px;\n    height: 25px;\n    margin-left: -1px;\n    border-left: 1px solid $color;\n    content: '';\n  }\n\n  &::after {\n    position: absolute;\n    right: calc(\n      50% - 3.5px\n    ); // subpixel value to align point of arrow w/ border above\n    bottom: -30px;\n    border-top: 5px solid $color;\n    border-right: 5px solid transparent;\n    border-left: 5px solid transparent;\n    content: '';\n  }\n}\n","// sendgrid style-guide variables\n\n$font-path: '../fonts' !default;\n$border-radius: 2px;\n\n// modular scale base\n// modularscale.com/?12,20&px&1.5\n$scale-base: 12px;\n\n// modular scale up\n$scale-base-up-01: 13px;\n$scale-base-up-02: 15px;\n$scale-base-up-03: 18px;\n$scale-base-up-04: 20px;\n$scale-base-up-05: 27px;\n$scale-base-up-06: 30px;\n$scale-base-up-07: 40px;\n$scale-base-up-08: 45px;\n$scale-base-up-09: 60px;\n$scale-base-up-10: 90px;\n\n// modular scale down\n$scale-base-down-01: 9px;\n$scale-base-down-02: 6px;\n$scale-base-down-03: 3px;\n\n// colors - primary\n$sg-blue: #1a82e2;\n$slate: #294661;\n\n// colors - primary shades\n$sg-blue-80: #489be8;\n$sg-blue-60: #76b4ee;\n$sg-blue-40: #a3cdf3;\n$sg-blue-20: #d1e6f9;\n$sg-blue-10: #e8f2fc;\n$sg-blue-05: #f3f9fd;\n$sg-blue-02: #fbfdff;\n\n$slate-80: #546b81;\n$slate-60: #7f90a0;\n$slate-40: #a9b5c0;\n$slate-20: #d4dadf;\n$slate-10: #e9ecef;\n$slate-05: #f4f6f7;\n$slate-02: #fbfbfc;\n\n// colors - logo accents\n$logo-accent-light: #99e1f4;\n$logo-accent-medium: #00b3e3;\n$logo-accent-dark: #009dd9;\n\n// colors - charting\n$ron-burgundy: #b71c1c;\n$burnt-orange: #ff5722;\n$carrot: #f90;\n$mango: #fdd835;\n$mantis: #18c96e;\n$spr